@import url(https://fonts.googleapis.com/css2?family=Heebo:wght@900&family=Montserrat:wght@600&display=swap);
body {
    font-family: "Heebo", sans-serif;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #8de0e2;
}

#root {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container {
    background-color: #e5dada;
    border-radius: 25px;
}

.progress-value {
    font-family: "Heebo", sans-serif;
    letter-spacing: 1px;
    background-color: rgb(4, 15, 22);
    border-radius: 25px;
    color: rgb(235, 235, 235);
    height: 100%;
    text-align: center;
}



